import React, { useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { useHistory } from 'react-router';
import { Alert, Col, CustomInput, Row } from 'reactstrap';
import { ERROR, findByExample, InputGroupInline, showToast, WindowComponent, WindowComponentProps } from 'summer';
import { getDefaultPrinterDataBasic, PrinterDataBasic } from '../../../../../models/dtos/ger/printerDataBasic';
import { CLASS_NAME, ConfigLivroMLF } from '../../../../../models/dtos/mlf/configLivroMLF';
import { DATES } from '../../../../../models/enumerated/ger/printerConfigTypeEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import ReportFormView from '../../../report/ReportFormView';
import { CiapRelPass, CLASS_KEY, REPORT_EVENT, REPORT_TITLE } from './ciapRelRV';

const BASIC_DATA: PrinterDataBasic = {
  ...getDefaultPrinterDataBasic(REPORT_TITLE, REPORT_EVENT, CLASS_KEY, DATES.value),
  dtFinal: '31/12/2017',
  dtInicial: '01/12/2017',
  module: 'MLF',
};

export default function CiapReportView(props: WindowComponentProps) {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const history = useHistory();
  const formProps = useForm<CiapRelPass & PrinterDataBasic>({ mode: 'onBlur' });

  const configLivroMLFExample: ConfigLivroMLF = {
    idGerEmpresa: globalReducer.globalParameter.empresa.id,
  };

  useEffect(
    () => {
      if (globalReducer.globalParameter.empresa) {
        findByExample(configLivroMLFExample, CLASS_NAME, {
          errorFunction: mensagem => {
            showToast(mensagem, ERROR);
          },
          thenFunction: (result: ConfigLivroMLF) => {
            setTimeout(() => {
              formProps.setValue('pageNumber', String(result.ciapFolha));
              formProps.triggerValidation('pageNumber');
              formProps.setValue('idGerEmpresa', globalReducer.globalParameter.empresa.id);
              formProps.triggerValidation('idGerEmpresa');
            }, 1000);
          },
        });
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  return (
    <WindowComponent {...props} title={REPORT_TITLE}>
      <ReportFormView formProps={formProps} printerData={BASIC_DATA} history={history}>
        <InputGroupInline formProps={formProps} id="idGerEmpresa" hidden={true} />

        <Row>
          <Col>
            <CustomInput
              type="switch"
              label="Armazenar Página (Livro Oficial)"
              id="armazenarPaginaInicial"
              name="armazenarPaginaInicial"
              innerRef={formProps.register}
            />
          </Col>
        </Row>

        {globalReducer.globalParameter.empresaMLF.ciapCoeficienteCreditamentoDecimais ? (
          <Alert variant="danger" className="text-center">
            Considerando {globalReducer.globalParameter.empresaMLF.ciapCoeficienteCreditamentoDecimais} decimais no cálculo do Coeficiente de
            Creditamento.
          </Alert>
        ) : null}
      </ReportFormView>
    </WindowComponent>
  );
}
